import emptyView from '../../components/emptyview.vue';

export default {
	data() {
		return {
			list: [],
			pageNum: 1,
			pageSize: 10,
			total: 1,
			statesDetail: ['等待医生接诊中...', '正在问诊','', '已结束问诊','已取消问诊']
		}
	},
	components: {
		emptyView
	},
	onLoad(options) {


	},
	onShow() {
		this.pageNum = 1;
		this.total = 1;
		this.list = [];
		this.getUserInquiry();
	},
	onReady() {

	},
	onReachBottom() {
		this.getUserInquiry();
	},

	methods: {
		getUserInquiry:function() {
			let that = this;
			if (that.total <= (that.pageNum - 1) * that.pageSize) {
				return;
			}
			const params = {};
			params.pageNum = that.pageNum;
			params.pageSize = that.pageSize;
			that.$util.request({
				url: "/didi-patient/inquiryinfo/queryPage",
				param: params,
				success: function(res) {
					that.list = that.list.concat(res.data.list);
					that.pageNum += 1;
					that.total = res.data.total;
					for (var i = 0; i < that.list.length; i++) {
						that.list[i].picUrl = that.list[i].picUrl.split(',')[0];
					}
				},
				error: function() {}
			})
		},
		toInquiryDetail:function(inquiryId){
			uni.navigateTo({
				url:'/pages/my/inquiryDetail?inquiryId='+inquiryId
			})
		}
		
	}
}
